@use "global" as *;

.section-header{
  text-align: center;
  @include mq(md){
    position: relative;
  }
}

.section-header__title{
  font-weight: $regular;
  font-size: rem(20);
  letter-spacing: rem(2);
  @include mq(md){
    font-size: rem(30);
  }
}

.section-header__title--en{
  color: $yellow;
  font-weight: $regular;
  font-size: rem(50);
  letter-spacing: rem(2);
  opacity: .2;
  font-family: $second-font-family;
  @include mq(md){
    font-size: rem(110);
    display: flex;
    max-width: rem(1150);
    margin: 0 auto;
  }
}
