@use "global" as *;

.main-imgBox {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.main-imgBox__img {
  z-index:10;
  opacity: 0;
  width: 100%;
  height: 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-animation: anime 18s 0s infinite;
  animation: anime 18s 0s infinite;
}

.main-imgBox__img:nth-of-type(2) {
-webkit-animation-delay: 6s;
animation-delay: 6s; }

.main-imgBox__img:nth-of-type(3) {
-webkit-animation-delay: 12s;
animation-delay: 12s; }

@keyframes anime {
  0% {
      opacity: 0;
  }
  16% {
      opacity: 1;
  }
  34% {
      opacity: 1;
  }
  50% {
      opacity: 0;
        transform: scale(1.2);
                  z-index:9;
  }
  100% {
      opacity: 0
  }
}

