@use "global" as *;

.btn{
  display: inline-block;
  padding: rem(10) 0;
  background-color: $gray;
  color: $white;
  border: 1px solid $white;
  font-size: rem(20);
  letter-spacing: rem(2);
  white-space: nowrap;
  width: 100%;
  text-align: center;
  max-width: rem(560);
  @include mq(md){
    width: rem(360);
    font-size: rem(16);
    padding: rem(17) 0;
  }
}

