@use "global" as *;

.footer{
  padding-top: rem(51);
  padding-bottom: rem(48);
  @include mq(md){
    padding-top: rem(29);
    padding-bottom: rem(26);
    padding-right: rem(32);
    padding-left: rem(32);
  }
}

.footer__link img{
  display: inline-block;
  text-align: center;
  width: rem(70);
}

.footer__copyright{
  text-align: center;
  margin-top: rem(32);
  font-weight: $regular;
  font-size: rem(11);
  letter-spacing: rem(1.2);
  @include mq(md){
    margin-top: rem(70);
    font-size: rem(15);
  }
}
