@use "global" as *;

$color01: #fff;
$color02: #fff;

body {
  margin: 0;
  box-sizing: border-box;
}

.hamburger__button {
  display: inline-block;
  text-decoration: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.hamburger {
  position: fixed;
  top: rem(-3);
  right: rem(10);
  z-index: 5;
  width: rem(48);
  height: rem(48);
  box-shadow: 0 0 2rem transparent;
  outline: none;
  -webkit-transition: all .3s ease-in-out;
          transition: all .3s ease-in-out;
}


.hamburger__line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: rem(25);
  height: rem(2);
  background-color: $color01;
  -webkit-transition: inherit;
          transition: inherit;
  &::before,
  &::after {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background-color: inherit;
    content: '';
    -webkit-transition: inherit;
            transition: inherit;
  }
}
.drawer-background {
  position: fixed;
  z-index: 3;
  width: 100vw;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  background-color: #000;
  color: #fff;
  visibility: hidden;
  opacity: 0;
}
.is-drawerActive {
  .sp-global-menu {
    transform: translateX(rem(0));
  transition: .3s;

  }
  .drawer-background {
    visibility: visible;
    opacity: 0.8;
    transition: .3s;
  }
}
.hamburger__line::before {
  top: rem(-6);
}
.hamburger__line::after {
  top: rem(6);
}

//メニュー開封時
.hamburger[aria-expanded="true"] .hamburger__line {
  background-color: transparent;
  &::before,
  &::after {
  top: 0;
  background-color: $color02;
  }
}

//メニュー開封時
.hamburger[aria-expanded="true"] .hamburger__line::before {
  //バツボタンの角度
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

//メニュー開封時
.hamburger[aria-expanded="true"] .hamburger__line::after {
  //バツボタンの角度
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.sp-global-menu {
  position: fixed;
  z-index: 4;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  height: 100vh;
  background-color: #000;
  color: #fff;
  transform: translateX(rem(1000));
  transition: .3s;
  text-align: center;

  display: flex;
  align-items: center;
  justify-content: center;
}

.hamburger__ttl{
  margin-left: rem(38);
  font-family: 'Montserrat', sans-serif;
  font-size: rem(10);
  color: white;
}

//メニュー開封時
.hamburger[aria-expanded="true"] .hamburger-ttl {
  color: white;
}
