@use "global" as *;

.footer-nav__list{
  @include mq(md){
    display: flex;
    justify-content: space-between;
  }
}

.footer-nav__wrapper{
  @include mq(md){
    display: flex;
  }
}

.footer-nav__item{
  text-align: center;
  &+&{
    margin-top: rem(7.5);
    @include mq(md){
      margin-top: 0;
      margin-left: rem(30);
    }
  }
}

.footer-nav__logo{
  @include mq(md){
    display: flex;
    align-items: center;
  }
}

.footer-nav__link{
  @include mq(md){
    font-weight: $regular;
    font-size: rem(16);
    line-height: calc(35/16);
    letter-spacing: rem(2);
    padding-bottom: rem(3);
  }
}

.footer-nav__link:hover{
  @include mq(md){
    border-bottom: 1px solid #FFEA2E;
    transition: all .3s;
  }
}


.footer-nav__link img{
  display: inline-block;
  width: rem(70);
  @include mq(md){
    margin-top: rem(3);
  }
}
