@use "global" as *;

.pagetop{
  position: fixed;
  right: rem(20);
  bottom:rem(40);
  background-color: $white;
  width: rem(45);
  height: rem(45);
  text-align: center;
  z-index: 9999;
  @include mq(md){
    right: rem(32);
    bottom: rem(32);
    cursor: pointer;
  }
}

.pagetop:hover{
  @include mq(md){
    background-color: $gray;
    transition: .3s;
    .pagetop__arrow{
      border-color: $white $white transparent transparent;
    }
  }
}

.pagetop__arrow{
  width: rem(22);
  height: rem(22);
  border: 3px solid;
  border-color: $gray $gray transparent transparent;
  transform: rotate(-45deg);
  position: absolute;
  top: 41%;
  left: 28%;

}
