@use "global" as *;

.works__inner{
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  @include mq("md") {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
    background: linear-gradient(90deg, transparent 0%, transparent 30%, $gray 30%, $gray 100%);
  }
}

.works__header-title--en{
  @include mq(md){
    justify-content: flex-end;
    max-width: rem(1150);
    margin: 0 auto;
    padding: 0 rem(25);
  }
}

.works__header-title{
  margin-top: rem(-25);
  @include mq(md){
    margin-top: rem(-115);
  }
}

.works__block{
  margin-top: rem(47);
  @include mq(md){
    display: flex;
    padding: rem(80) 0 rem(40) 0;
    width: 100%;
    margin-top: rem(33);
    justify-content: flex-start;
    align-items: center;
  }
}

.works__block-content{
  padding: 0 rem(20) rem(13);
  @include mq(md){
    margin-left: rem(33);
    margin-top: rem(-35);
    padding: 0;
  }
}

.works__img{
  position: relative;
}

.works__btn{
  margin-top: rem(26);
  @include mq(md){
    margin-top: rem(50);
    font-weight: $medium;
  }
}
