@use "global" as *;

.swiper {
  width: 100%;
  background-color:$gray;


  @include mq(md){
    width: rem(557);
    height: rem(430);
    background-color: transparent;
  }
}

.swiper-pagination {
  position:initial;
  @include mq(md){
    position: absolute;
  }
}

.swiper-pagination-bullet {
  background: #C4C4C4;
  &+&{
    margin-left: rem(10);
  }
}

.swiper-pagination-bullet-active {
  background: $yellow;
  opacity: 1;
}

.swiper-slide{
  position: relative;
  width: 100%;

  @include mq(md){
    max-width: rem(557);
  }
}

.swiper-slide img{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: cover;
  height: 100%;

  @include mq(md){
    height: 90%;/* 別サイズ画像に対応 */
  }
}

//比率管理
.swiper-slide::before{
  content: "";
  display: block;
  padding-top: 69.8%; /* 高さを幅の75%に固定 */
}

//マスク
.swiper-slide::after{
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.2);
  z-index: 9998;


  @include mq(md){
    height: 90%;
  }
}

