@use "global" as *;

.main-view{
  position: relative;
  height: 100vh;
}

//マスク
.main-view::before{
  position: absolute;
  content: "";
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.2);
}

.main-view__block{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $white;
  white-space: nowrap;
  text-align: center;
  z-index: 9998;
}

.main-view__title{
  font-weight: $bold;
  font-size: rem(28);
  line-height: 1;
  letter-spacing: rem(0.5);
  @include mq(md){
    font-size: rem(50);
  }
}

.main-view__subtitle{
  font-weight: $bold;
  font-size: rem(20);
  line-height: 1;
  letter-spacing: rem(0.5);
  margin-top: rem(24);
  @include mq(md){
    font-size: rem(25);
  }
}


