@use "global" as *;

/* ボタン共通設定 */
.btn-hover {
  /*線の基点とするためrelativeを指定*/
  position: relative;
  display:inline-block;
  color:$gray;
  text-decoration: none;
  outline: none;
  /*はみ出す背景色を隠す*/
  overflow: hidden;

  z-index:0;
}

/*hoverした際のボタンの形状*/
.btn-hover:hover {
  color:$gray;
}

/*背景の設定*/
.btn-hover::before{
  content: '';
  /*絶対配置で線の位置を決める*/
  position: absolute;
  left: 0;
  bottom:0;
  z-index: -1;
  /*背景の形状*/
  height: 100%;
  width: 0;
  background:$white;
  /*アニメーションの設定*/
  transition: all .3s;
}

/*hoverをすると背景が伸びる*/
.btn-hover:hover::before{
  width: 100%;
}


.btn-hover--white::before{
  background:$gray;
}

.btn-hover--white:hover {
  color:$white;
  border: $gray;
}


