@use "global" as *;

.contact__header-title--en{
  @include mq(md){
    justify-content: flex-start;
  }
}

.contact__header-title{
  margin-top: rem(-25);
  @include mq(md){
    margin-top: rem(-75);
  }
}

.contact{
  background-color: $gray;
}

.contact__txt{
  font-weight: $regular;
  font-size: rem(16);
  line-height: 1.43;
  letter-spacing: rem(2);
  margin-top: rem(20);
  @include mq(md){
    text-align: center;
  }
}

.contact__wrapper{
  text-align: center;
}

.contact__btn{
  margin-top: rem(46);
  @include mq(md){
    margin-top: rem(58);
    font-size: rem(20);
    line-height: 2.9;
    padding: 0;
  }
}
