@use "global" as *;

.section-card{
  display: inline-block;
  position: relative;
  max-width: rem(600);
  width: 100%;
  background-color: $white;

  display: flex;
  flex-direction: column;
  margin-right: auto;
  margin-left: auto;
  &+&{
    margin-top: rem(24);
    @include mq(md){
      margin-top: 0;
      margin-left: rem(25);
    }
  }
  @include mq(md){
    max-width: calc(33.333% - 1.5625rem * 2 / 3);
  }
}

.section-card:nth-child(3n+1){
  @include mq(md){
    margin-left: 0;
  }
}

.section-card:nth-child(n+4){
  @include mq(md){
    margin-top: rem(48);
  }
}

.section-card:hover{
  background-color: $gray;
  transition: all .3s;
  .section-card__title,.section-card__txt,.section-card__date{
    color: $white;
  }
}

.section-card__contents{
  text-align: left;
  padding: rem(4) rem(23) 0;
  color: $black;
  transition: all  .3s ease;
  max-width: rem(600);
}

.section-card__img img{
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.section-card__img{
  position: relative;
  width: 100%;
}
.section-card__img:before {
  content:"";
  display: block;
  // padding-top: 66.6%; /* 高さを幅の75%に固定 */
  aspect-ratio: 350/233;
}
.section-card__img img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: cover;
  height: 100%;
}

.section-card__content{
  display: flex;
  justify-content: space-between;
  margin-top: rem(3);

  padding: 0 rem(23) rem(16);
  color: $black;

  @include mq(md){
    margin-top: auto;
    padding-top: rem(10);
  }
}

.section-card__title{
  font-weight: $medium;
  font-size: rem(18);
  line-height: calc(35/18);
  letter-spacing: rem(1);
}

.section-card__txt,.section-card__category,.section-card__date{
  font-weight: $regular;
  font-size: rem(14);
  line-height: calc(26/14);
  letter-spacing: rem(1);
}

.section-card__txt{
  margin-top: rem(4);
}

.section-card__category{
  padding: rem(1) rem(4);
  background-color: #FFEA2E;
}

.section-card__new{
  color: $gray;
  font-weight: $regular;
  font-size: rem(16);
  line-height: calc(35/16);
  letter-spacing: rem(2);
  padding: rem(12) rem(11);
  border-radius: 50%;
  background-color: #FFBA6A;
  position: absolute;
  z-index:9998;
  top: -4%;
  left: -4%;
}


