@use "global" as *;

.section-block{
  max-width: rem(600);
  width: 100%;
  margin-right:auto;
  margin-left:auto;
  @include mq(md){
    max-width: rem(1100);
  }
}

.section-block__content{
  padding: rem(24) rem(20) rem(21);
  background-color: $gray;
  @include mq(md){
    background: transparent;
    width: rem(510);
    padding: 0;
  }
}

.section-block__title{
  text-align: center;
  font-weight: $regular;
  font-size: rem(20);
  letter-spacing: rem(2);
  @include mq(md){
    text-align-last: left;
    font-size: rem(25);
    letter-spacing: rem(.5);
  }
}

.section-block__txt{
  margin-top: rem(24);
  font-weight: $regular;
  font-size: rem(16);
  letter-spacing: rem(2);
  line-height: 1.43;
  @include mq(md){
    line-height: calc(35/16);
    margin-top: rem(35);
    letter-spacing: rem(.5);
  }
}
