@use "global" as *;

.header{
  height: rem(50);
  z-index: 9999;
  background: rgba(17,17,17,0.5);
  width: 100%;
  position: fixed;
}

.header__inner{
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 rem(22);
  @include mq(md){
    padding-right: 0;
    padding-left: rem(34);
  }
}

.header__logo{
  display: flex;
  align-items: center;
}

.header__logo--drawer{
  position: absolute;
  top: 1.8%;
  left: 6.5%;
}

.header__link{
  display: inline-block;
}

.header__link img{
  width: rem(70);
  @include mq(md){
    width: rem(94);
  }
}



