@use "global" as *;

.overview__inner{
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  @include mq("md") {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
    background: linear-gradient(90deg, $gray 0%, $gray 70%, transparent 70%, transparent 100%);
  }
}

.overview__header-title--en{
  @include mq(md){
    justify-content: flex-start;
    margin-left: rem(90);
    padding: 0 rem(25);
  }
}

.overview__header-title{
  margin-top: rem(-25);
  @include mq(md){
    margin-top: rem(-114);
  }
}

.overview__block{
  margin-top: rem(48);
  @include mq(md){
    display: flex;
    flex-direction: row-reverse;
    padding: rem(80) 0;
    align-items: center;
    width: 100%;
    margin-top: rem(35);
    justify-content: center;
  }
}

.overview__block-content{
  @include mq(md){
    width: rem(510);
    margin-top: rem(-10);
  }
}

.overview__img{
  position: relative;
  width: 100%;
}
.overview__img:before {
  content:"";
  display: block;
  padding-top: 69.8%; /* 高さを幅の75%に固定 */
}
.overview__img img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: cover;
  height: 100%;/* 別サイズ画像に対応 */
}


.overview__img{
  @include mq(md){
    margin-left: rem(33);
    max-width: rem(557);
  }
}


.overview__btn{
  margin-top: rem(24);
  @include mq(md){
    margin-top: rem(48);
    font-weight: $medium;
  }
}
