@use "global" as *;

.container{
  position: relative;
}

.container__line--1{
  position: absolute;
  top: 9.7%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: -1;
  @include mq(md){
    top: 20.3%;
  }
}

.container__line--2{
  position: absolute;
  top: 38.9%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: -1;
  @include mq(md){
    top: 61.3%;
  }
}
