@use "global" as *;

.news__inner{
  max-width: rem(600);
  @include mq(md){
    max-width: rem(1090);
  }
}

.news__items{
  @include mq(md){
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.news__item{
  display: flex;
  align-items: center;
}

.news__date{
  font-weight: $regular;
  font-size: rem(16);
}

.news__category{
  font-weight: $regular;
  font-size: rem(14);
  padding: rem(6) rem(22);
  border: 1px solid $white;
  margin-left: rem(31);
  white-space: nowrap;
  @include mq(md){
    margin-left: rem(45);
    font-weight: $medium;
    padding: rem(4) rem(22);
  }
}

.news__title{
  display: inline-block;
  font-weight: $regular;
  font-size: rem(16);
  margin-top: rem(12);
  @include mq(md){
    margin-top: 0;
    margin-left: rem(13);
    cursor: pointer;
    max-width: rem(578);
    width: 100%;
  }
}

.news__title:hover{
  @include mq(md){
    border-bottom: 1px solid $white;
    transition: .3s;
  }
}

.news__btn{
  margin-top: rem(28);
  @include mq(md){
    margin-top: 0;
    width: rem(134);
    min-width: rem(134);
    padding: rem(4) 0;
    color: #000;
    background-color: $white;
    font-weight: $medium;
    font-size: rem(16);
    letter-spacing: 0;
  }
}
