@use "global" as *;

.content__header-title--en{
  @include mq(md){
    justify-content: flex-start;
    margin-left: rem(90);
    padding: 0 rem(25);
  }
}

.content__header-title{
  margin-top: rem(-25);
  @include mq(md){
    margin-top: rem(-123);
  }
}

.content__items{
  display: flex;
  flex-wrap: wrap;
  margin-top: rem(48);
  @include mq(md){
    margin-top: rem(42);
  }
}

.content__item{
  position: relative;
  width: 50%;
  @include mq(md){
    width: 25%;
  }
}

.content__title{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: $regular;
  font-size: rem(20);
  letter-spacing: rem(2);
  white-space: nowrap;
  @include mq(md){
    font-size: rem(25);
  }
}

.content__item::before{
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9998;
}
.content__item:hover::before{
  background-color: rgba($color: $white, $alpha: 0.6);
  transition: .3s;
}

.content__img{
  position: relative;
  width: 100%;
}
.content__img:before {
  content:"";
  display: block;
  padding-top: 89.36%; /* 高さを幅の75%に固定 */
  @include mq(md){
    padding-top: 100%; /* 高さを幅の75%に固定 */
  }
}
.content__img img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: cover;
  height: 100%;/* 別サイズ画像に対応 */
}







