@use "global" as *;

.header-nav{
  width: 100%;
  @include mq(md){
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
  }
}

.header-nav__list,.header-nav__item{
  width: 100%;
  @include mq(md){
    height: inherit;
  }
}
.header-nav__list{
  @include mq(md){
    display: flex;
    align-items: center;
  }
}
.header-nav__item{
  font-size: rem(16);
  letter-spacing: rem(2);
  &+&{
    margin-top: rem(31);
    @include mq(md){
      margin-top: 0;
      margin-left: rem(30);
      white-space: nowrap;
    }
  }
}
.header-nav__item:first-child{
  @include mq(md){
    margin-top: 0;
    white-space: nowrap;
  }
}

.header-nav__link{
  display: inline-block;
  width: 100%;
  @include mq(md){
    position: relative;
    letter-spacing: rem(2);
    height: inherit;
    display: flex;
    align-items: center;
  }
}
.header-nav__link::before{
  @include mq(md){
    position: absolute;
    top: 80%;
    left: 0;
    content: "";
  }
}
.header-nav__link:hover::before{
  height: 1px;
  width: 100%;
  background-color: $yellow;
  transition: all .3s;
}

.header-nav__link--white{
  width: 100%;
  @include mq(md){
    position: relative;
    letter-spacing: rem(2);
    display: inline-block;
    height: inherit;
    display: flex;
    align-items: center;
  }
}

.header-nav__btn{
  @include mq(md){
    color: $black;
    background: $white;
    padding: rem(13) rem(32);
  }
}

.header-nav__btn:hover{
  @include mq(md){
    background-color: $gray;
    color: $white;
    transition: all .3s;
  }
}



